//NAV
.nav {
  background-color: goldenrod;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 70px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}
.nav-logo {
  height: 50px;
  width: 50px;
}
.nav-logo:hover {
  cursor: pointer;
}
.nav .nav-content {
  width: 90%;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  //border:1px solid red;
}
@media(min-width: 768px){
  .nav .nav-content{
    width:80%;
    //border:1px solid blue;
  }
}

ul.nav-items{
  margin-bottom:0; //BP ADDED TO CENTER MENU
}
.nav-item {
  display: inline;
  margin-left: 2rem;
  margin-top:2rem;
  color: #333;
  font-size: 20px;
  font-weight:bold;
}
.nav-item:hover {
  color: black;
  cursor: pointer;
}
.nav-item .active{
  color:green !important;
  text-decoration:none;
}


